<template>
  <div class="page-register">
    <div class="columns">
      <div class="column is-4 is-offset-4">
        <h1 class="title">Register</h1>
        <form @submit.prevent="submitForm()">
          <div class="field">
            <label>Username</label>
            <div class="control">
              <input type="text" class="input" v-model="username" />
            </div>
          </div>
          <div class="field">
            <label>Password</label>
            <div class="control">
              <input type="password" class="input" v-model="password" />
            </div>
          </div>
          <div class="field">
            <label>Repeat Password</label>
            <div class="control">
              <input type="password" class="input" v-model="password2" />
            </div>
          </div>
          <div class="notification is-danger" v-if="errors.length">
            <p v-for="error in errors" :key="error">{{ error }}</p>
          </div>
          <div class="field">
            <div class="control">
              <button class="button is-dark">Register</button>
            </div>
            <hr />
            <p>
              Or <router-link to="/login">click here</router-link> to login!
            </p>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { toast } from "bulma-toast";
export default {
  name: "Register",
  data() {
    return {
      username: "",
      password: "",
      password2: "",
      errors: [],
    };
  },
  methods: {
    async submitForm() {
      this.errors = [];
      if (this.username === "") {
        this.errors.push("The username is missing!");
      }
      if (this.password === "" || this.password2 === "") {
        this.errors.push("Password must be not empty!");
      }
      if (this.password2 !== this.password) {
        this.errors.push("The password doesn't match!");
      }

      if (!this.errors.length) {
        const formData = {
          username: this.username,
          password: this.password,
        };
        await axios
          .post("/api/v1/users/", formData)
          .then((response) => {
            toast({
              message: "Account created, you should login now",
              type: "is-success",
              dismissible: true,
              pauseOnHover: true,
              duration: 2000,
              position: "bottom-right",
            });

            this.$router.push("/login");
          })
          .catch((err) => {
            if (err.response) {
              for (const property in err.response.data) {
                this.errors.push(`${property}: ${err.response.data[property]}`);
              }
              console.log(JSON.stringify(err.response.data));
            } else if (err.message) {
              this.errors.push("Something went wrong. Please try again");
              console.log(JSON.stringify(err));
            }
          });
      }
    },
  },
  mounted() {
    document.title = "Register | DFashion";
  },
};
</script>

